<template>
  <v-app id="app">
    <v-toolbar id="nav" dark app color="primary" clipped-left> 
      <router-link to="/" class="clickable ml-3 mr-4" tag="span">
        <span class="title">Source&nbsp;<span class="font-weight-light">Bound</span></span>
      </router-link>
      <router-link to="/dashboard" class="clickable mx-2" tag="span">
        <span>Dashboard</span>
      </router-link>
      <router-link to="/about" class="clickable mx-2" tag="span">
        <span>About</span>
      </router-link>
      <v-spacer/>
      <profile/>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <v-snackbar
      v-model="snackbar"
      bottom
      :timeout="2000"
    >
      {{ text }}
      <v-btn
        color="primary"
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import { EventBus } from '@/plugins/event-bus.js';
import Profile from "@/components/Profile.vue"

export default {
  name: 'App',
  components: {
    Profile
  },
  data() {
    return {
      text: "",
      snackbar: false
    }
  },
  created() {
    EventBus.$on('snackbar', text => {
      this.text = text
      this.snackbar = true
    });
  }
}
</script>

<style scoped>
.clickable {
  cursor: pointer;
}
</style>
