import '@babel/polyfill'
import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import router from './router'
import NutritionLabel from 'vue-nutrition-label';
import 'vue-nutrition-label/dist/vue-nutrition-label.min.css'

var Parse = require('parse');

Vue.use(NutritionLabel);

Parse.initialize("SourceBound");

Parse.serverURL = 'https://sourcebound.herokuapp.com/parse'

Vue.prototype.$Parse = Parse

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')