<template>
  <router-link v-if="!currentUser" to="/login" class="clickable mx-2" tag="span">
    <v-btn outline>
        <span>Sign In</span>
    </v-btn>
  </router-link>
  <v-menu v-else offset-y>
    <v-btn
      slot="activator"
      flat
      :loading="loading"
    >
      <v-icon class="mr-2">account_circle</v-icon>
      <span>{{currentUser.getUsername()}}</span>
    </v-btn>
    <v-list>
      <v-list-tile
        v-for="(item, index) in items"
        :key="index"
        @click="item.action()"
      >
        <v-icon class="mr-3">{{item.icon}}</v-icon>
        <v-list-tile-title>{{item.name}}</v-list-tile-title>
      </v-list-tile>
    </v-list>
  </v-menu>
</template>

<script>
import { EventBus } from '@/plugins/event-bus.js';

export default {
  created() {
    this.onUser();
  },
  data() {
    return {
      currentUser: this.$Parse.User.current(),
      loading: false,
      items: [
        {
          name: "Settings",
          action: () => console.log("to Settings"),
          icon: "settings"
        },
        {
          name: "Logout",
          action: () => {
            this.loading = true
            this.$Parse.User.logOut().then(() => {
              this.updateUser()
              EventBus.$emit('snackbar', "Logged out successfully.");
              this.loading = false
            }, error => {
              console.error(error)
              this.updateUser()
              EventBus.$emit('snackbar', "Error logging out.");
              this.loading = false
            }
            )
          },
          icon: "logout"
        },
      ]
    }
  },
  methods: {
    onUser() {
      EventBus.$on('userUpdate', () => {
        this.updateUser();
      });
    },
    updateUser() {
      this.currentUser = this.$Parse.User.current();
    }
  }
}
</script>